html, body { 
  height:100%; 
  min-height:100%; 
  font-family: $avenir; 
  color: $gray-500;
  font-size: rem(16px);
}

// this is area is for reserved class
#main-container { 
  width: 100%;
  min-height: 100%;
	position: relative;
	flex-direction: column;
  display: flex;
  z-index: 1;

}

main {
  flex-direction: column;
  position: relative;
  flex: 1 1 auto;
  z-index: 1;
}

.hide { position:relative; }
.ease,a,input[type="submit"],button { @include transition(all 0.5s ease); }
.ease2 { @include transition(all 0.3s ease); }
.loader { position:relative; overflow:hidden; background:#fff url('../images/page_template/loading.gif') no-repeat center; }
.auto { max-width:$maxWidth; margin-left: auto; margin-right: auto; }
.img-auto { width: 100%; height: auto; }
.img-objectfit { height: 100%; object-fit: cover; object-position: center; }
.container {
  max-width: 1420px;
}

.zx1 { z-index: 1; }
.zx2 { z-index: 2; }
.zx3 { z-index: 3; }
.zx4 { z-index: 4; }
.zx5 { z-index: 5; }
.zx6 { z-index: 6; }
.overflow { overflow: hidden; }
.pl-0 { padding-left: 0; }
.pr-0 { padding-right: 0; }

.preloader { position:fixed; width:100%; height:100%; overflow:hidden; }
.explorer .preloader { background:#fff url('../images/page_template/loading.gif') no-repeat center; }
.explorer .preloader .spinner { display:none; }
// this is area is for reserved class

// you can include here the rest of your class name eg. home page

h2 {
  font-size: rem(24px);
  font-weight: 700;
  color: $siteColor;
}

.btn {

  &.btn-outline {
    border: 1px solid $coBlue;
    color: $coBlue;
    
    &:hover {
      background: $coBlue;
      color: $coWhite;
    }
  }

  &.btn-orange {
    border-radius: 0;
    border-color: $gray-300;
    color: $coOrange;

    &:hover {
      border-color: $coOrange;
      background: $coOrange;
    }
  }

  &.btn-blue {
    background: $coBlue;
    color: $coWhite;
    border-radius: 0;

    &:hover {
      background: darken($color: $coBlue, $amount: 10%);
    }
  }
}

